.tagline {
	font-size: 21px;
	margin-left: 21px;
	color: #000;
	line-height: 1.2;
	text-transform: uppercase;
	font-weight: 400;
	font-style: normal;
	margin-left: 0px;
	display: block;
	margin: 1em auto 0;
}
.tagline-mobile {
	text-align: center;
	margin: 1em auto;
	max-width: 290px;
}
.q-presented-by {
	line-height: 1;
}
.q-header__container {
    flex-direction: column;
	margin-bottom: 20px;
}

@media (min-width: 720px) {
	.tagline-mobile {
		display: none;
		margin-left: 10px;
	}
	.tagline {
		margin: 0
	}
	.q-header__container {
		flex-direction: row;
		margin-bottom: 0;
	}
	.tagline-mobile {
		display: none;
		margin-left: 10px;
	}
	.tagline-desktop {
		display: block;
		padding-right: 25px;
		border-right: 1px solid #262626;
	}
	.q-presented-by{
		color: #000;
		font-family: Gotham;
		font-size: 12px;
		font-style: normal;
		font-weight: 400;
		line-height: normal;
		margin-left: 25px;
		margin-right: 9px;
		display: block;
	}
}
.dead-button {
	align-items: center;
    appearance: none;
    background: #ddd;
    border: 0;
    border-radius: 2px;
    color: #FFF;
    display: inline-flex;
    flex-flow: nowrap row;
    font-family: Gotham, sans-serif;
    font-size: 15px;
    font-style: normal;
    font-weight: 500;
    justify-content: center;
    letter-spacing: -0.015em;
    line-height: 44px;
    margin: 0;
    min-width: 9.33333em;
    max-width: 140px;
    padding: 0 18px;
    position: relative;
    text-align: center;
    text-decoration: none;
    transition: background 200ms, color 200ms;
    vertical-align: middle;
    white-space: nowrap;
}
.buttons-wrapper {
	display: flex;
	justify-content: center;
	flex-direction: column;
	text-align: center;
}
.qsc-usa .q-button {
	margin-bottom: 0.5em;
}
.qsc-usa .q-link {
	max-width: 140px;
}
.q-find-qsc__table-container .q-button {
	max-width: 140px;
}
.q-square__link {
	margin-top: 27px;
}
.q-square__link a {
	color: #FFF;
	text-decoration: none;
	display: flex;
	align-items: center;
}
.q-square__link a:focus {
	outline: 0;
}
.q-square__link a img {
	margin-right: 10px;
	margin-left: 4px;
}
.fancybox-slide {
	box-sizing: content-box;
	max-width: 960px;
	padding: 0 calc(50% - 480px);
	position: relative;
}
.fancybox-slide--iframe .fancybox-content {
	position: relative;
	padding-bottom: 56.25%; /* 16:9 */
	height: 0;
	margin: 0;
	max-height: 100%;
	max-width: 100%;
	width: 100%;
}
.fancybox-slide--iframe .fancybox-content iframe {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}
.q-default-hero__content {
	display: none;
}
.q-find-qsc__copy {
	max-width: 480px;
	margin: 0;
	line-height: 1.5;
}
.q-find-qsc__heading {
	margin: 0 0 19px;
}
.q-find-qsc__table-container {
	padding-top: 63px;
}
.q-find-qsc__table-row {
	display: flex;
	font-family: Gotham, sans-serif;
	border-bottom: 1px solid #9B9B9B;
	padding-bottom: 20px;
	margin-bottom: 26px;
	min-height: 68px;
	flex-direction: column;
}
@media (min-width: 480px) {
	.q-find-qsc__table-row {
		flex-direction: row;
		padding-bottom: 10px;
	}
}
.q-qsc-find__title {
	font-weight: 500;
	margin-bottom: 10px;
}
@media (min-width: 480px) {
	.q-qsc-find__title {
		margin-bottom: 0;
		width: 30.8%;
		padding-right: 15px;
	}
}
.q-qsc-find__title a {
	text-decoration: none;
	color: #262626;
	font-size: 16px;
	line-height: 1.2;
	transition: color 0.3s;
}
.q-qsc-find__title a:hover {
	color: #7b7b7b;
}
.q-qsc-find__date,
.q-qsc-find__location {
	display: flex;
	padding-right: 15px;
	font-size: 16px;
	line-height: 1.2;
}
.q-qsc-find__date {
	display: block;
}
@media (min-width: 480px) {
	.q-qsc-find__date {
		width: 54.4%;
	}
}
.q-qsc-find__date img,
.q-qsc-find__location {
	display: none;
}
.q-qsc-find__cta {
	text-align: left;
	flex: 1 0 0;
}
@media (min-width: 720px) {
	.q-qsc-find__date,
	.q-qsc-find__location {
		width: 27.2%;
		display: flex;
	}
	.q-qsc-find__date {
		display: flex;
	}
	.q-qsc-find__date span,
	.q-qsc-find__location span {
		margin-right: 16px;
	}
	.q-qsc-find__date img {
		display: block;
		width: 32px;
		max-width: none;
	}
	.q-qsc-find__location img {
		width: 23px;
		max-width: none;
	}
}
@media (min-width: 980px) {
	.q-qsc-find__date {
		padding-top: 6px;
	}
	.q-qsc-find__location {
		padding-top: 3px;
	}
	.q-qsc-find__date,
	.q-qsc-find__location {
		font-size: 18px;
		line-height: 29px;
	}
	.q-qsc-find__title a {
		font-size: 18px;
		line-height: 1.8;
	}
	.q-qsc-find__date span,
	.q-qsc-find__location span {
		display: block;
	}
}
.q-container__highlights {
	padding-top: 80px;
	padding-bottom: 80px;
}
@media (min-width: 980px) {
	.q-container__highlights {
		padding-top: 161px;
		padding-bottom: 206px;
	}
}
.q-container__highlights .q-grid__column:last-child {
	padding-top: 60px;
}
@media (min-width: 720px) {
	.q-container__highlights .q-grid__column:last-child {
		padding-top: 0;
	}
}
.q-highlights__heading {
	padding: 0;
	max-width: 675px;
	margin: 0 0 68px;
	line-height: 1.25;
}
@media (min-width: 1080px) {
	.q-highlights__heading {
		line-height: 1.15;
	}
}
.q-highlights__paragraph {
	margin: 0;
	max-width: 455px;
	line-height: 1.85;
}
.q-qsc__banner-home {
	margin-top: 43px;
	border-radius: 2px;
}
.q-qsc-highlights__blocks {
	padding-left: 19px;
}
.q-qsc-highlights-block__item {
	padding-bottom: 31px;
}
.q-icon-plus-content {
	padding-top: 6px;
}
.q-icon-plus-content__icon {
	margin-bottom: 1.25em;
	margin-right: 1.098em;
	height: auto;
	width: 4.65em;
}
.q-icon-plus-content__heading {
	font-weight: 500;
	font-size: 17px;
}
@media (min-width: 1290px) {
	.q-icon-plus-content__heading {
		font-size: 17px;
		font-weight: 500;
		line-height: 23px;
		letter-spacing: -0.21px;
	}
}
.q-qsc-register__gray {
	background-color: #6A778B;
}
.q-qsc-register__gray:hover {
	background-color: #8291a9;
}
.q-qsc-register__gray--light,
.q-qsc-register__gray--light:hover {
	background-color: #DDD;
}
.q-qsc-register__light-gray {
	background-color: #DDDDDD;
}
.q-qsc-register__light-gray:hover {
	background-color: #DDDDDD;
}
.q-qsc-register__light-gray--light,
.q-qsc-register__light-gray--light:hover {
	background-color: #DDDDDD;
}
.q-qsc__location-text {
	display: block;
	margin-top: 10px;
}
.q-qsc__location-text.has-date {
	margin-top: 1px;
}
@media (min-width: 480px) {
	.q-qsc__location-text {
		margin-top: 20px;
	}
	.q-qsc__location-text.has-date {
		margin-top: 1px;
	}
}
.q-qsc__location-text br {
	display: none;
}
@media (min-width: 720px) {
	.q-qsc__location-text {
		display: none;
	}
}
.q-past-events__heading {
	margin-top: 69px;
	margin-bottom: 0;
}
.no-divider {
	border: none;
}
.past-events-container {
	margin-bottom: -31px;
}
.q-qsc-find__title{
	color:#262626;
	font-size: 16px;
	line-height: 1.2;
}
@media (min-width:980px) {
	.q-qsc-find__title{
		font-size: 18px;
		line-height: 1.8
	}
}
.q-qsc__logo img {
	height: 36px;
}
.q-qsc__logo {
	width: auto;
	height:36px;
	display: block;
    flex: none;
    margin: 0 auto 0;
    opacity: 1;
    overflow: hidden;
    position: relative;
    text-decoration: none;
    text-indent: 100%;
    transition: opacity 200ms;
}
.q-qsc-find__location {
	display: none;
}

@media (min-width:720px) {
	.q-qsc-find__title {
		width: 48%;
	}
	.q-qsc-find__date {
		width: 50%;
	}
}
.q-default-hero--square-baseline .q-default-hero__splash {
    margin-top: 0;
    padding-top: 0;
}
@media (min-width: 720px){
	.q-default-hero__container {
		padding-top: 0;
	}
	.q-default-hero--square-baseline .q-default-hero__splash {
		padding-top: 0%;
	}
	.q-default-hero__image::before {
		content: "";
		display: block;
		 padding-bottom: 0; 
	}
	.heroBox {
		padding-top: 200px;
	}
}


.q-default-hero__image {
    display: block;
}

.q-crs__header--navigation {
	display: none;
	margin-bottom: 20px;
}
@media (min-width: 720px) {
.q-crs__header--navigation {
    display: block;
    flex: 1 0 auto;
    text-align: right;
	margin-bottom: 0;
 }
}
.q-crs__menu {
    display: inline-flex;
    list-style: none;
    margin: 0;
    padding: 0 38px 0 0;
}
.q-crs__menu > li:last-child {
    margin-right: 0;
}
.q-crs__menu li > a {
    font-family: Gotham, sans-serif;
    font-size: 15px;
    color: #262626;
    text-decoration: none;
    display: block;
    height: 100%;
    transition: color 0.3s;
}
.q-virtual-event {
	background: #6CA3CF;
	font-family: Gotham;
	font-size: 14px;
	font-weight: 500;
	color: #fff;
	padding:5px 6px;
	width: max-content;
	position: absolute;
	bottom: 0px;
	left: 20px;
}
@media (min-width:720px) {
	.q-virtual-event {
		display: none;
	}
}
@media (min-width:920px) {
	.q-virtual-event {
		left: 60px;
		font-size: 20px;
		bottom: 0;
		display: block;
		padding: 12px;
	}
}

@media (min-width:1200px) {
	.q-virtual-event {
		left: 110px;
		font-size: 20px;
	}
}
.q-square__heading {
	text-transform: uppercase;
	border-left: 1px solid #fff;
	padding-left: 40px;
}
.q-container__find-qsc {
	margin-top: 50px;
}
@media (min-width: 720px){
	.q-default-hero__box-wrap {
		width: 40.31008%;
		min-width: 420px;
	}
}
.q-default-hero__image::before {
    content: "";
    display: block;
    padding-bottom: 0;
}
.q-default-hero__box-wrap {
    width: 100%;
	margin-top: 20px;
}
.q-default-hero__image {
	position: relative;
}
.q-default-hero__image {
	min-height: auto;
}
@media (min-width:720px) {
	.q-default-hero__image {
		position: absolute
	}
	.q-default-hero__box-wrap {
		width:40.31008%;
		margin-top: 0px;
	}
}